#tableWrapper{
    margin: 0 auto;
    max-width: 100%;
    max-height: 100%;
    overflow-x: scroll;
    overflow-y: scroll;
}

#mindScapeUl {
    table-layout: fixed;
    width: 3000px;
    border-collapse: collapse;
    border: 3px solid black;
}
thead{
    height: 150px;
}
tbody tr {
    height: 150px;
    width: 30px;
}

tbody tr:nth-child(odd) {
    background-color: rgba(0, 0, 0, .25);
}
tbody td {
    height: 150px;
    width: 30px;
    padding: 20px;
}

tbody td:nth-child(2) {
    background-color: rgba(255, 192, 203, .25);
}
tbody td:nth-child(3) {
    background-color: rgba(254, 137, 240, 0.25);
}
tbody td:nth-child(4) {
    background-color: rgba(162, 162, 255, 0.25);
}
tbody td:nth-child(5) {
    background-color: rgba(109, 233, 255, 0.25);
}
tbody td:nth-child(6) {
    background-color: rgba(103, 251, 179, 0.25);
}
tbody td:nth-child(7) {
    background-color: rgba(132, 255, 149, 0.25);
}
tbody td:nth-child(8) {
    background-color: rgba(193, 252, 74, 0.25);
}
tbody td:nth-child(9) {
    background-color: rgba(255, 228, 92, 0.25);
}
tbody td:nth-child(10) {
    background-color: rgba(255, 201, 92, 0.25);
}
tbody td:nth-child(11) {
    background-color: rgba(255, 130, 92, 0.25);
}


.value {
    font-size: 78px;
    padding-left: 16px;
    padding-right: 16px;
    margin-top: 2px;
    font-family: 'Courier New', Courier, monospace;
}

.button {
    appearance: none;
    background: none;
    font-size: 32px;
    padding-left: 12px;
    padding-right: 12px;
    outline: none;
    border: 2px solid transparent;
    color: rgb(112, 76, 182);
    padding-bottom: 4px;
    cursor: pointer;
    background-color: rgba(112, 76, 182, 0.1);
    border-radius: 2px;
    transition: all 0.15s;
}

.textbox {
    font-size: 32px;
    padding: 2px;
    width: 64px;
    text-align: center;
    margin-right: 4px;
}

.button:hover,
.button:focus {
    border: 2px solid rgba(112, 76, 182, 0.4);
}

.button:active {
    background-color: rgba(112, 76, 182, 0.2);
}

.asyncButton {
    composes: button;
    position: relative;
}

.asyncButton:after {
    content: '';
    background-color: rgba(112, 76, 182, 0.15);
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    opacity: 0;
    transition: width 1s linear, opacity 0.5s ease 1s;
}

.asyncButton:active:after {
    width: 0%;
    opacity: 1;
    transition: 0s;
}